<template>
    <div class="collections">
        <!-- 收藏 -->
        <van-search v-model="value" placeholder="请输入搜索关键词"
                @search="onSearch"
        
        />
        <van-empty v-if="!list.length" description="空空入也" />
        <div class="content">
            <div class="img" v-for="item in list" :key="item.id">
                <van-image
                fit="cover"
                style="width: 3.3rem;height:3.5rem"
                :src="item.src"
                />
                <p>{{item.name}}</p>
                <van-button type="danger" block @click="del(item.id)">删除</van-button>
            </div>
        </div>
    </div>
</template>

<script>
import {mapState} from "vuex"
import { Toast } from 'vant';

export default {
    data() {
    return {
        value: '',
    }},

    computed: {
        ...mapState("collection", ["list"]),
    },
    methods: {
        del(id) {
            this.$store.commit("collection/del",id)
        },
        onSearch(val) {
            Toast("功能暂未开放");
            this.value = ""
        },
    }
}
</script>

<style lang="scss" scoped>

    .collections{
    background-color: rgb(232, 232, 232);
    height: 100vh;
    .img{
        background-color: #fff;
        width: 150px !important;
        text-align: center;
        margin-bottom: 10px;
        }
        .content{
            padding: 10px 20px;
            flex-wrap: wrap;
            
            display: flex;
            align-items: flex-start;
            .img{
                border-radius: 10px;
                box-sizing: border-box;
                padding: 15px 5px;
                margin-bottom: 15px;
                margin-right: 25px;
                &:nth-child(even) {
                    margin-right: 0px;
                }
                p{
                    margin: 5px 0;
                }
                .van-button{
                    box-sizing: border-box;
                    background-clip: content-box;
                    border: none;
                }
            }
        }
    }
</style>